import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { EntriesParams } from '../models/entries.model';
import { LoggerOperationResult } from '../models/logger-operation-result.model';
import { catchError, tap, map } from 'rxjs/operators';
import { Observable, of, BehaviorSubject } from 'rxjs';
import { StompRService, StompConfig, StompState } from '@stomp/ng2-stompjs';
import { Message } from '@stomp/stompjs';

@Injectable({
    providedIn: 'root'
})
export class TbLoggerService {
    public loggerUrl: string;
    private howMany = 100;

    public mqConnectionState: StompState = StompState.CLOSED;
    public mqConnectionStateObservable: BehaviorSubject<StompState> = new BehaviorSubject(StompState.CLOSED);

    constructor(@Inject('env') private env, private http: HttpClient, public stompService: StompRService) {
        if (env.stompConfig) this.mqInit();
    }

    mqInit() {
        if (this.env.stompConfig) {
            this.stompService.config = this.env.stompConfig;
            this.stompService.initAndConnect();
            this.stompService.state.subscribe((status: StompState) => {
                this.mqConnectionState = status;
                this.mqConnectionStateObservable.next(status);
            });
        }
    }

    /**
     * Ritorna la base url del logger,
     * caricata da un file di configurazione caricato dinamicamente (assets/environment.json)
     */
    getLoggerUrl() {
        return this.loggerUrl ? this.loggerUrl : this.env.logger.url;
    }

    /**
     * Console.log in attesa di post to logger
     *
     * @param message
     * @param optionalParams
     */
    log(message?: any, ...optionalParams: any[]): void {
        console.log(message, ...optionalParams);
    }

    /**
     * Console.log in attesa di post to logger
     *
     * @param message
     * @param optionalParams
     */
    debug(message?: any, ...optionalParams: any[]): void {
        console.log(message, ...optionalParams);
    }

    /**
     * Console.warn in attesa di post to logger
     *
     * @param message
     * @param optionalParams
     */
    warn(message?: any, ...optionalParams: any[]): void {
        console.warn(message, ...optionalParams);
    }

    /**
     * Console.error in attesa di post to logger
     *
     * @param message
     * @param optionalParams
     */
    error(message?: any, ...optionalParams: any[]): void {
        console.error(message, ...optionalParams);
    }

    /**
     * Return logs: LoggerOperationResult
     *
     * @param params: EntriesParams
     */
    public getLogs(params: EntriesParams): Observable<boolean | LoggerOperationResult> {
        if (!params.appId) {
            const error = new LoggerOperationResult();
            error.Result = false;
            error.Message = 'Error - No appId, no party';
            return of(error);
        }

        const url = this.getLoggerUrl() + `entries/${params.appId}`;

        let p = new HttpParams();
        p = p.append('howMany', '' + this.howMany);
        if (params.apps) p = p.append('apps', params.apps);
        if (params.appTypes) p = p.append('appTypes', params.appTypes);

        const httpOptions = {
            params: p
        };

        return this.http.get<LoggerOperationResult>(url, httpOptions).pipe(
            tap(op => console.log('TbLoggerService.getLogs', op)),
            catchError(this.handleError('TbLoggerService.getLogs', false))
        );
    }

    public getApps(appId: string): Observable<boolean | LoggerOperationResult> {
        if (!appId) {
            const error = new LoggerOperationResult();
            error.Result = false;
            error.Message = 'Error - No appId, no party';
            return of(error);
        }

        const url = this.getLoggerUrl() + `apps/${appId}`;

        return this.http.get<LoggerOperationResult>(url).pipe(
            tap(op => console.log('TbLoggerService.getApps with appId: ', appId, op)),
            catchError(this.handleError('TbLoggerService.getApps', false))
        );
    }

    public getAppTypes(appId: string): Observable<boolean | LoggerOperationResult> {
        if (!appId) {
            const error = new LoggerOperationResult();
            error.Result = false;
            error.Message = 'Error - No appId, no party';
            return of(error);
        }

        const url = this.getLoggerUrl() + `appTypes/${appId}`;

        return this.http.get<LoggerOperationResult>(url).pipe(
            tap(op => console.log('TbLoggerService.getAppTypes with appId: ', appId, op)),
            catchError(this.handleError('TbLoggerService.getAppTypes', false))
        );
    }

    /**
     * Handle Http operation that failed.
     * Let the app continue.
     * @param operation - name of the operation that failed
     * @param result - optional value to return as the observable result
     */
    public handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {
            console.error(`${operation} failed: ${error.message}`);

            // Let the app keep running by returning an empty result.
            return of(result as T);
        };
    }

    /**
     * Connessione a RabbitMQ
     */
    public mqConnect(queueName: string) {
        return this.stompService.subscribe(queueName).pipe(map((msg: Message) => JSON.parse(msg.body)));
    }

    public mqConnected() {
        return this.stompService.connected();
    }
    public mqDisconnect() {
        return this.stompService.disconnect();
    }
}
